@import url(https://fonts.googleapis.com/css?family=KoHo|Roboto);
h1 {
  text-align: center;
  margin-top: 10px; }

ul {
  list-style: none;
  position: absolute;
  display: flex;
  text-align: center;
  flex-flow: column;
  width: 80%;
  left: -100%;
  background-color: #fff;
  height: 70%;
  z-index: 1;
  margin: 0; }

li {
  text-decoration: none;
  padding: 10px;
  font-family: "KoHo", sans-serif;
  color: #000;
  margin: 30px;
  font-size: 20px;
  border-bottom: 0.5px solid grey; }

.hamburger-container {
  display: flex;
  justify-content: flex-end;
  align-items: center; }

.hamburger-label {
  padding: 20px; }

.hamburger-checkbox:checked ~ .navbar-list {
  left: 0;
  -webkit-transition: left 0.2s ease-out;
  transition: left 0.2s ease-out; }

.hamburger-container .hamburger-bar1,
.hamburger-container .hamburger-bar2,
.hamburger-container .hamburger-bar3 {
  width: 30px;
  height: 3px;
  background-color: #000;
  margin: 6px 0; }

.hamburger-checkbox:checked
~ .hamburger-container
.hamburger-label
.hamburger-bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 4.5px);
          transform: rotate(-45deg) translate(-9px, 4.5px); }

.hamburger-checkbox:checked
~ .hamburger-container
.hamburger-label
.hamburger-bar2 {
  opacity: 0; }

.hamburger-checkbox:checked
~ .hamburger-container
.hamburger-label
.hamburger-bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -3.5px);
          transform: rotate(45deg) translate(-8px, -3.5px); }

.hamburger-checkbox {
  display: none; }

.search-page-wrapper {
  background-image: url("/dog-3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top; }

.search-container {
  width: 100%;
  height: 120vh;
  display: flex;
  flex-direction: column;
  background-color: rgba(250, 250, 250, 0.6);
  font-family: "KoHo", sans-serif;
  text-align: center; }

h1 {
  font-family: "KoHo", sans-serif;
  margin-bottom: 15px; }

h3 {
  margin: 0;
  font-size: 20px; }

.search-container-sex,
.search-container-size,
.search-container-age,
.search-container-catOk {
  padding: 30px;
  font-size: 15px; }
  .search-container-sex input,
  .search-container-size input,
  .search-container-age input,
  .search-container-catOk input {
    margin-left: 20px; }
  .search-container-sex label,
  .search-container-size label,
  .search-container-age label,
  .search-container-catOk label {
    margin-left: 10px; }

input[type=checkbox] {
  -ms-transform: scale(1.5);
  /* IE */
  -moz-transform: scale(1.5);
  /* FF */
  -webkit-transform: scale(1.5);
  /* Safari and Chrome */
  -o-transform: scale(1.5);
  /* Opera */
  padding: 10px; }

.submit-button {
  margin: 20px auto 0;
  position: relative;
  width: 170px;
  display: flex;
  justify-content: center; }

.submit-button input {
  -webkit-appearance: none;
  padding: 20px 80px;
  background-color: #2e2d2b;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin-bottom: 20px;
  border-radius: 10px; }

.submit-button #paw-print {
  width: 30px;
  position: absolute;
  top: 20%;
  right: 90%; }

.matches-container {
  background-color: #fbf4ed; }

@media (min-width: 900px) {
  .hamburger-container {
    display: flex;
    justify-content: center; }
  .search-container {
    width: 40%;
    height: 100%;
    margin: 0 auto;
    border-radius: 20px; }
  .result-page-wrapper {
    width: 70%;
    padding: 0px 10px; }
  .submit-button {
    margin-top: 40px; }
  .result-page-container img {
    width: 30%; }
  .hamburger-label {
    display: none; }
  ul {
    left: 0;
    position: relative;
    display: flex;
    flex-flow: row;
    justify-content: flex-end;
    height: 20px;
    padding: 60px; }
  li {
    margin: 0;
    border: none; } }

@media (min-width: 600px) and (max-width: 1200px) {
  .search-container {
    width: 60%;
    height: auto;
    margin: 0 auto;
    border-radius: 20px; }
  .submit-button img {
    left: 10%; } }

